import React, { Component } from "react";
import { Flex, WingBlank, WhiteSpace, NavBar, Toast, Icon } from "antd-mobile";
import { Link } from "react-router-dom";
import styles from "./index.module.css";
import { login } from "../../utils/api/user";
import { setToken } from "../../utils/currentCity";
import { withFormik } from "formik";
import * as yup from "yup";

// 
const REG_UNAME = /^[a-zA-Z_\d]{5,8}$/;
const REG_PWD = /^[a-zA-Z_\d]{5,12}$/;

class Login extends Component {
  state = {
    username: "", // 
    password: "", // 
  };

  render() {
    const {
      values,
      // touched,
      errors,
      handleChange,
      handleSubmit,
    } = this.props;
    return (
      <div className={styles.root}>
        {/*  */}
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => this.props.history.goBack()}
        >
          
        </NavBar>
        <WhiteSpace size="xl" />

        {/*  */}
        <WingBlank>
          <form onSubmit={handleSubmit}>
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="username"
                placeholder=""
                value={values.username}
                onChange={handleChange}
              />
            </div>
            {/* 58 */}
            {errors.username && (
              <div className={styles.error}>{errors.username}</div>
            )}
            <div className={styles.formItem}>
              <input
                className={styles.input}
                name="password"
                type="password"
                placeholder=""
                value={values.password}
                onChange={handleChange}
              />
            </div>
            {/* 512 */}
            {errors.password && (
              <div className={styles.error}>{errors.password}</div>
            )}
            <div className={styles.formSubmit}>
              <button className={styles.submit} type="submit">
                 
              </button>
            </div>
          </form>
          <Flex className={styles.backHome}>
            <Flex.Item>
              <Link to="/registe">~</Link>
            </Flex.Item>
          </Flex>
        </WingBlank>
      </div>
    );
  }
}

export default withFormik({
  // inputname
  mapPropsToValues: () => ({ username: "test2", password: "test2" }),

  // 
  validationSchema: yup.object().shape({
    username: yup
      .string()
      .required("")
      .matches(REG_UNAME, "58"),
    password: yup
      .string()
      .required("")
      .matches(REG_PWD, "512"),
  }),

  // 
  // handleSubmit: (values: Values, formikBag: FormikBag) => void | Promise<any>
  handleSubmit: async (values, formikBag) => {
    const { username, password } = values;
    const res = await login(username, password);
    const { status, data, description } = res;
    const { history, location } = formikBag.props;
    if (status === 200) {
      // token
      setToken(data.token);
      Toast.success(description, 2);
      if (!!location.backUrl) {
        history.replace(location.backUrl);
      } else {
        history.replace("/");
      }
    } else {
      Toast.fail(description);
    }
  },
})(Login);
