import React, { Component } from "react";

import {
  Flex,
  List,
  InputItem,
  Picker,
  ImagePicker,
  TextareaItem,
  Modal,
  NavBar,
  Icon,
  Toast,
} from "antd-mobile";

import HousePackage from "../../../components/HousePackage";

import styles from "./index.module.css";

import { uploadHouseImgs } from "../../../utils/api/plugin";
import { pubHouse } from "../../../utils/api/user";

const alert = Modal.alert;

// 
const roomTypeData = [
  { label: "", value: "ROOM|d4a692e4-a177-37fd" },
  { label: "", value: "ROOM|d1a00384-5801-d5cd" },
  { label: "", value: "ROOM|20903ae0-c7bc-f2e2" },
  { label: "", value: "ROOM|ce2a5daa-811d-2f49" },
  { label: "+", value: "ROOM|2731c38c-5b19-ff7f" },
];

// 
const orientedData = [
  { label: "", value: "ORIEN|141b98bf-1ad0-11e3" },
  { label: "", value: "ORIEN|103fb3aa-e8b4-de0e" },
  { label: "", value: "ORIEN|61e99445-e95e-7f37" },
  { label: "", value: "ORIEN|caa6f80b-b764-c2df" },
  { label: "", value: "ORIEN|dfb1b36b-e0d1-0977" },
  { label: "", value: "ORIEN|67ac2205-7e0f-c057" },
  { label: "", value: "ORIEN|2354e89e-3918-9cef" },
  { label: "", value: "ORIEN|80795f1a-e32f-feb9" },
];

// 
const floorData = [
  { label: "", value: "FLOOR|1" },
  { label: "", value: "FLOOR|2" },
  { label: "", value: "FLOOR|3" },
];

export default class RentAdd extends Component {
  constructor(props) {
    super(props);

    this.state = {
      // 
      tempSlides: [],

      // id
      community: {
        name: "",
        id: "",
      },
      // 
      price: "",
      // 
      size: "",
      // 
      roomType: "",
      // 
      floor: "",
      // 
      oriented: "",
      // 
      title: "",
      // 
      houseImg: "",
      // 
      supporting: "",
      // 
      description: "",
    };
  }

  componentDidMount() {
    const { id, name, data } = this.props.location;
    // 
    if (!!data) {
      this.setState({
        ...data,
      });
    }
    // 
    if (!!id && !!name) {
      const com = {};
      com.name = name;
      com.id = id;
      this.setState({
        community: com,
      });
    }
  }

  // 
  onCancel = () => {
    alert("", "?", [
      {
        text: "",
        onPress: async () => this.props.history.go(-1),
      },
      {
        text: "",
      },
    ]);
  };

  // 
  getValue = (name, v) => {
    console.log(name, v);
    this.setState({
      [name]: v,
    });
  };

  // 
  // files: Object, operationType: string, index: number
  getImg = (files, operationType, index) => {
    console.log(files, operationType, index);
    this.setState({
      tempSlides: files,
    });
  };

  // 
  selPack = (selNames) => {
    console.log(selNames);
    this.setState({
      supporting: selNames.join("|"),
    });
  };

  // 
  addHouse = async () => {
    const {
      tempSlides,
      title,
      description,
      oriented,
      supporting,
      price,
      roomType,
      size,
      floor,
      community,
    } = this.state;
    // 
    if (!title || !size || !price) {
      return Toast.info("", 1);
    }
    // 
    let houseImg;
    if (tempSlides.length) {
      let fm = new FormData();
      tempSlides.forEach((item) => fm.append("file", item.file));
      let res = await uploadHouseImgs(fm);
      console.log(res);
      if (res.status === 200) {
        houseImg = res.data.join("|");
      } else {
        Toast.fail(res.description, 2);
      }
    }
    // 
    const otd = {
      title,
      description,
      houseImg,
      oriented,
      supporting,
      price,
      roomType,
      size,
      floor,
      community: community.id,
    };
    let ores = await pubHouse(otd);
    console.log("form all data:", otd);
    if (ores.status === 200) {
      Toast.success("", 1, () => {
        this.props.history.replace("/rent");
      });
    } else {
      if (ores.status === 400) {
        Toast.info("", 1, () => {
          // 
          this.props.history.push({
            pathname: "/login",
            backUrl: this.props.location.pathname,
          });
        });
      }
    }
  };

  render() {
    const Item = List.Item;
    const { history } = this.props;
    const {
      community,
      price,
      size,
      roomType,
      floor,
      oriented,
      description,
      tempSlides,
      title,
    } = this.state;

    return (
      <div className={styles.root}>
        <NavBar
          className={styles.navHeader}
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={this.onCancel}
        >
          
        </NavBar>
        <List
          className={styles.header}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          {/*  */}
          <Item
            extra={community.name || ""}
            arrow="horizontal"
            onClick={() =>
              history.replace({ pathname: "/rent/search", data: this.state })
            }
          >
            
          </Item>
          <InputItem
            placeholder="/"
            extra="/"
            type="number"
            value={price}
            onChange={(v) => this.getValue("price", v)}
          >
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
          </InputItem>
          <InputItem
            placeholder=""
            extra=""
            type="number"
            value={size}
            onChange={(v) => this.getValue("size", v)}
          >
            
          </InputItem>
          <Picker
            data={roomTypeData}
            value={[roomType]}
            cols={1}
            onChange={(v) => this.getValue("roomType", v[0])}
          >
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>

          <Picker
            data={floorData}
            value={[floor]}
            cols={1}
            onChange={(v) => this.getValue("floor", v[0])}
          >
            <Item arrow="horizontal"></Item>
          </Picker>
          <Picker
            data={orientedData}
            value={[oriented]}
            cols={1}
            onChange={(v) => this.getValue("oriented", v[0])}
          >
            <Item arrow="horizontal">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            </Item>
          </Picker>
        </List>

        <List
          className={styles.title}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <InputItem
            placeholder="  2 5000"
            value={title}
            onChange={(v) => this.getValue("title", v)}
          />
        </List>

        <List
          className={styles.pics}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <ImagePicker
            files={tempSlides}
            multiple={true}
            className={styles.imgpicker}
            onChange={this.getImg}
          />
        </List>

        <List
          className={styles.supporting}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <HousePackage
            select
            onSelect={this.selPack}
            data={this.props.location.data?.supporting}
          />
        </List>

        <List
          className={styles.desc}
          renderHeader={() => ""}
          data-role="rent-list"
        >
          <TextareaItem
            rows={5}
            placeholder=""
            autoHeight
            value={description}
            onChange={(v) => this.getValue("description", v)}
          />
        </List>

        <Flex className={styles.bottom}>
          <Flex.Item className={styles.cancel} onClick={this.onCancel}>
            
          </Flex.Item>
          <Flex.Item className={styles.confirm} onClick={this.addHouse}>
            
          </Flex.Item>
        </Flex>
      </div>
    );
  }
}
