import React, { Component } from "react";
import { getCities, getHotCity } from "../../utils/api/area";
import { getCurrCity, setSession } from "../../utils/currentCity";
import "react-virtualized/styles.css";
import { List, AutoSizer } from "react-virtualized";
import { NavBar, Icon, Toast } from "antd-mobile";
import "./index.scss";

class index extends Component {
  state = {
    cityIndex: [],
    cityList: {},
    activeIndex: 0,
  };
  componentDidMount() {
    this.loadData();
  }

  // 
  loadData = async () => {
    // 
    let res = await getCities();

    // 
    let { cityIndex, cityList } = this.formatCities(res);

    // 
    let hotCity = await getHotCity();
    if (res.status === 200) {
      cityIndex.unshift("hot");
      cityList["hot"] = hotCity.data;
    }

    // 
    let currCity = await getCurrCity();
    cityIndex.unshift("#");
    cityList["#"] = [currCity];

    this.setState({
      cityIndex,
      cityList,
    });
  };

  // 
  formatCities = (res) => {
    let cityList = {},
      cityIndex;
    if (res.status !== 200) {
      return { cityList, cityIndex };
    }

    // 
    res.data.map((item) => {
      let head = item.pinyin.slice(0, 1); //

      //()
      if (head in cityList) {
        cityList[head].push(item);
      } else {
        cityList[head] = [item];
      }
    });

    // 
    cityIndex = Object.keys(cityList).sort();

    return { cityList, cityIndex };
  };

  // ()
  formatLetter(letter, first) {
    switch (letter) {
      case "hot":
        return first ? "" : "";
      case "#":
        return first ? "" : "";
      default:
        return letter.toUpperCase();
    }
  }

  // 
  getRowheight = ({ index }) => {
    const { cityIndex, cityList } = this.state;
    let letter = cityIndex[index];
    // title+*
    return 36 + 50 * cityList[letter].length;
  };

  // 
  // 
  changeCity = (city) => {
    const hasData = ["", "", "", ""];
    if (hasData.includes(city.label)) {
      setSession(city);
      this.props.history.goBack();
    } else {
      Toast.info("", 2);
    }
  };

  // 
  renderCityIndex = () => {
    const { cityIndex, activeIndex } = this.state;
    return cityIndex.map((item, index) => {
      return (
        <li
          key={item}
          className="city-index-item"
          onClick={() => {
            // 
            this.listRef.scrollToRow(index);
          }}
        >
          <span className={activeIndex === index ? "index-active" : ""}>
            {this.formatLetter(item, true)}
          </span>
        </li>
      );
    });
  };

  // 
  onRowsRendered = ({ startIndex }) => {
    if (this.state.activeIndex !== startIndex) {
      this.setState({
        activeIndex: startIndex,
      });
    }
  };

  rowRenderer = ({
    key, // Unique key within array of rows
    index, // Index of row within collection
    isScrolling, // The List is currently being scrolled
    isVisible, // This row is visible within the List (eg it is not an overscanned row)
    style, // Style object to be applied to row (to position it)
  }) => {
    return (
      <div key={key} style={style} className="city-item">
        <div className="title">
          {this.formatLetter(this.state.cityIndex[index])}
        </div>
        {this.state.cityList[this.state.cityIndex[index]].map((item, k) => {
          return (
            <div
              className="name"
              onClick={() => {
                this.changeCity(item);
              }}
              key={k}
            >
              {item.label}
            </div>
          );
        })}
      </div>
    );
  };

  render() {
    return (
      <div className="cityList">
        {/*  */}
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          onLeftClick={() => this.props.history.goBack()}
        >
          
        </NavBar>
        {/*  */}
        <AutoSizer>
          {({ height, width }) => (
            <List
              width={width}
              height={height}
              rowCount={this.state.cityIndex.length}
              rowHeight={this.getRowheight}
              rowRenderer={this.rowRenderer}
              scrollToAlignment="start"
              onRowsRendered={this.onRowsRendered}
              ref={(map) => {
                this.listRef = map;
              }}
            />
          )}
        </AutoSizer>
        {/*  */}
        <ul className="city-index">{this.renderCityIndex()}</ul>
      </div>
    );
  }
}

export default index;
